import React from "react"
import { StaticQuery, graphql } from "gatsby"

const Description = () => (
  <StaticQuery
    query={graphql`
      {
        allContentJson(filter: { id: { eq: "step1.description" } }) {
          nodes {
            value
          }
        }
      }
    `}
    render={({ allContentJson }) => <p>{allContentJson.nodes[0].value}</p>}
  />
)

export default Description
